import * as React from 'react';
import { TBItemSlot } from '@ali/we-design-next';
import { AlignmentSelect } from './components/hoverToolbar/alignmentSelect';
import type { BiPlugin } from '../../types';

export default function createRenderHoverToolbar(
  config: any,
): BiPlugin['renderHoverToolbar'] {
  return (props, controller, getNode) => {
    const { buttonName, extraProps } = props;
    const { onOverlayVisibleChange } = extraProps;
    switch (buttonName) {
      case 'default':
        return (
          <TBItemSlot display="iconSelect" >
            <AlignmentSelect
              controller={controller}
              getNode={getNode}
              onOverlayVisibleChange={onOverlayVisibleChange}
              locale={config.locale || {}}
            />
          </TBItemSlot>
        );
      default: return null;
    }
  };
}
